@import './fonts.scss';

:root {
  font-family: 'DMSans' !important;

  --intergalactic-base: 'DMSans'; // main app text

  --dark-blue: #495281;
  --light-blue: #8ecdff;
  --dark-blue-text: #4f567c;
  --main-blue: #008ff8;
  --blue-500: #006dca; // for secondary text (like in GraphStatisticsInfoCard component)
  --blue-300: #2bb3ff; // icons, placeholders, main colors for chart
  --blue-50: #e9f7ff; // for background (donation cards hover)

  --dark-red: #b13832;
  --red-300: #ff8786; // secondary text, graphs
  --light-red: #eb5757; // additional text color (like errors, etc)

  --gray-main: #a9abb6;
  --gray-secondary: #6c6e79;
  --gray-800: #191b23; // active item for settings sidebar
  --gray-700: #2b2e38; // dark shade for charts, bold text for switches' labels
  --gray-600: #484a54; // dark shade for charts,
  --gray-300: #a9abb6; // icons on the white background, main colors for charts, placeholders

  --gray-2: #4f4f4f; // switch label text on report issue and add feedback pages + descriptions

  --dark-yellow: #ffc43a; // background for the paypal button in donation sections

  --gray-card-hover: #edeef2;

  --intergalactic-border-info-active: var(--main-blue);
}

.dark-card {
  background: var(--dark-blue) !important;
  --intergalactic-text-link-hover-active: white;
  --intergalactic-text-link: var(--light-blue);

  --intergalactic-control-secondary-invert-active: white;

  button:active {
    --intergalactic-text-primary-invert: var(--dark-blue-text);
  }
}

.footer {
  --color-interactive_pdrly: var(--main-blue) !important;
}

.icon-wrapper {
  span {
    border: none !important;
  }
  svg {
    fill: #6c6e79;

    &:hover {
      fill: var(--main-blue) !important;
    }
  }
}

.delete-icon-wrapper {
  span {
    border: none !important;
  }
  svg {
    fill: #e46863;
    &:hover {
      fill: var(--dark-red) !important;
    }
  }
  &.non-active-icon {
    svg {
      fill: white;
      &:hover {
        fill: #ff9f9e !important;
      }
    }
  }
}

.donation-selector {
  --intergalactic-control-tertiary-info-active: var(--dark-blue) !important;
  --intergalactic-text-link: var(--gray-secondary) !important;

  --intergalactic-rounded-medium: 12px;

  .__active_16ooh_gg_ {
    --intergalactic-text-link: white !important;
  }

  button:hover {
    --intergalactic-control-tertiary-info-hover: none;
    --intergalactic-text-link: #006dca;
  }

  button:active {
    --intergalactic-text-link: white !important;
    background: var(--dark-blue) !important;
    color: white !important;
  }
}

.donation-card {
  cursor: pointer;
  &:not(:hover) {
    background: #f3f4f5;
  }
  &.active {
    background: white !important;
  }
  &:hover {
    --intergalactic-bg-primary-neutral: var(--blue-50) !important;
    background: var(--blue-50) !important;
  }

  &.gray:hover {
    --intergalactic-bg-primary-neutral: var(--gray-card-hover) !important;
    background: var(--gray-card-hover) !important;
  }
}

.fullscreen-page {
  width: 95vw !important;
  min-height: 100vh !important;
}

.inner-app {
  max-width: 1000px;
}
// sidebar
.reversed-list {
  li {
    position: relative;
    padding: 12px;
    border-bottom: 1px solid #e0e1e9;
    cursor: pointer;

    &:hover {
      background: #edeef2;
    }

    .___SMarker_za87k_gg_ {
      margin-left: 13px;
      opacity: 0;
      order: 1;
      margin-left: auto;
    }

    &.active {
      font-weight: 700;

      .___SMarker_za87k_gg_ {
        margin-top: 2px !important;
        opacity: 1;
      }
    }

    .___SContent_za87k_gg_ {
      order: 0;
      font-size: 16px;
      line-height: 21px;
    }
  }
}

.custom-slider {
  --intergalactic-spacing-5x: 30px;
}
.paypalButton {
  // background: var(--dark-yellow);
  --intergalactic-control-primary-info: var(--dark-yellow);
  --intergalactic-control-primary-info-hover: #e7b134;
  --intergalactic-control-primary-info-active: #bd9029;
  &:hover {
    // filter: brightness(0.95);
  }
}

.feedbackButton {
  --intergalactic-control-primary-info: var(--dark-blue);
  --intergalactic-control-primary-info-hover: #2c314d;
  --intergalactic-control-primary-info-active: #1b1e2f;
}

.popup-buttons-wrapper {
  button {
    --intergalactic-text-secondary: #4f567c;
    --intergalactic-control-secondary-neutral: #ffffff;
    --intergalactic-control-secondary-neutral-hover: #ededed;
    --intergalactic-control-secondary-neutral-active: #cfcfcf;
  }
}